body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav{
  background-color:#ffff66;
  display:flex;
  justify-content:center;
  align-items:center;
  width:100%;
  height:50px;
  border:2px solid #333;
  font-family:'Maven Pro';
  font-weight:bold;
  padding:10px;
}

.imgBar{
  margin-right:10px;
}

.text{
  font-size:1.4rem;
}

.searchBar{
  /* font-size: 24px;
  display: block;
  width:100%;
  padding:8px 0px;
  margin-top:10px;
  text-align:center; */
    width: 50%;
    padding: 10px 20px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    text-align:center;
    font-size:20px;
    margin:15px 0px;
    font-family: 'Maven Pro';
}

.App {
  text-align: center;
  background:#ccc;
}



.container{
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items:center;
    padding:30px;
    border:2px solid #333;
    margin : 0px 50px;
    margin-bottom:10px;
   
    
}

.poster{
    width:200px;
    border:3px solid #ffff66;
    border-radius: 10px;
    margin-right: 15px;
}

.details{
    display: flex;
    flex-direction: column;
    justify-content:space-between;
    align-items:center;
    font-family: 'Maven Pro';
    font-size:18px;
    
}

.btns{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.btn{
    width:75px;
    height:25px;
    background:#80002a;
    color:#fff;
    border:1px solid #000;
    border-radius: 6px;
    cursor:pointer;
}

@media(max-width:660px){
    .container{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }
}
